import { Injectable } from "@angular/core";
import { Subject } from "rxjs/Subject";

import { Recipe } from "./recipe.model";
import { Ingredient } from "../shared/ingredient.model";
import { ShoppingListService } from "../shopping-list/shopping-list.service";

@Injectable()
export class RecipeService {
  recipesChanged = new Subject<Recipe[]>();

  private recipes: Recipe[] = [
    new Recipe(
      "Super Tasty MoMo",
      "Tibetan special dish - just awesome!",
      "https://upload.wikimedia.org/wikipedia/commons/6/6b/Chicken_Momo_with_spicy_hot_curry.jpg",
      [new Ingredient("Meat", 1), new Ingredient("chilli", 3)]
    ),
    new Recipe(
      "Shapaley",
      "Go crazy after tasting this Tibetan dish",
      "https://s3.amazonaws.com/blogs-static.gazetadopovo.com.br/wp-content/uploads/sites/69/2017/12/2-Empanadas_Chile.jpeg",
      [new Ingredient("Meat", 3), new Ingredient("Soup", 2)]
    ),
    new Recipe(
      "Thukpa",
      "Try to stop yourself after tasting this!",
      "https://i0.wp.com/media.hungryforever.com/wp-content/uploads/2016/08/26153847/chicken-thukpa-recipe.jpg?fit=934%2C700&ssl=1",
      [new Ingredient("Meat", 1), new Ingredient("Soup", 1)]
    )
  ];

  constructor(private slService: ShoppingListService) {}

  setRecipes(recipes: Recipe[]) {
    this.recipes = recipes;
    this.recipesChanged.next(this.recipes.slice());
  }

  getRecipes() {
    return this.recipes.slice();
  }

  getRecipe(index: number) {
    return this.recipes[index];
  }

  addIngredientsToShoppingList(ingredients: Ingredient[]) {
    this.slService.addIngredients(ingredients);
  }

  addRecipe(recipe: Recipe) {
    this.recipes.push(recipe);
    this.recipesChanged.next(this.recipes.slice());
  }

  updateRecipe(index: number, newRecipe: Recipe) {
    this.recipes[index] = newRecipe;
    this.recipesChanged.next(this.recipes.slice());
  }

  deleteRecipe(index: number) {
    this.recipes.splice(index, 1);
    this.recipesChanged.next(this.recipes.slice());
  }
}
